import style from '../scss/page.module.scss';
import { Catalog, CatalogProps, Link, useLockBodyScroll } from 'topwrite';
import classNames from 'classnames';
import { IoChevronDown } from '@react-icons/all-files/io5/IoChevronDown';
import { MouseEvent, useCallback, useState } from 'react';
import { IoChevronForward } from '@react-icons/all-files/io5/IoChevronForward';
import { IoMenuSharp } from '@react-icons/all-files/io5/IoMenuSharp';
import { IoCloseSharp } from '@react-icons/all-files/io5/IoCloseSharp';

const Item: CatalogProps['itemComponent'] = ({ item, open, setOpen, activeDescendant, active, children }) => {

    const handleClick = useCallback((e: MouseEvent) => {
        e.preventDefault();
        e.stopPropagation();
        if (children) {
            setOpen(!open);
        }
    }, [open, children]);

    return <li className={classNames({ [style.open]: open })}>
        <div
            className={classNames(style.item, {
                [style.active]: active,
                [style.activeDescendant]: activeDescendant
            })}
        >
            <Link to={item}>{item.title}</Link>
            {children && <span onClick={handleClick} className={style.icon}>
                {open ? <IoChevronDown /> : <IoChevronForward />}
            </span>}
        </div>
        {children}
    </li>;
};

const Part: CatalogProps['partComponent'] = ({ part, children }) => {
    return <div className={style.part}>
        <div className={style.partTitle}>{part.title}</div>
        {children}
    </div>;
};

export default function Sidebar() {

    const [sidebarShown, setSidebarShown] = useState(false);

    useLockBodyScroll(sidebarShown);

    const showSidebar = useCallback(() => {
        setSidebarShown(true);
    }, [setSidebarShown]);
    const hideSidebar = useCallback(() => {
        setSidebarShown(false);
    }, [setSidebarShown]);

    const Icon = sidebarShown ? IoCloseSharp : IoMenuSharp;

    return <div className={classNames(style.sidebar, { [style.show]: sidebarShown })}>
        <span className={style.button} onClick={sidebarShown ? hideSidebar : showSidebar}>
            <Icon size={24} />
        </span>
        <div className={style.catalog} onClick={hideSidebar}>
            <Catalog
                itemComponent={Item}
                partComponent={Part}
            />
        </div>
    </div>;
}
