import style from '../scss/navbar.module.scss';
import {
    Link,
    Logo,
    NavLink,
    Search,
    useLockBodyScroll,
    useThemeConfig,
    useThemeContext,
    useTitle,
    useWindowSize,
    windowSizes
} from 'topwrite';
import Toggle from 'react-toggle';
import 'react-toggle/style.css';
import { useCallback, useEffect, useState } from 'react';
import { IoMenuSharp } from '@react-icons/all-files/io5/IoMenuSharp';
import classNames from 'classnames';

export default function Navbar() {

    const title = useTitle();
    const { isDarkTheme, setLightTheme, setDarkTheme } = useThemeContext();
    const [sidebarShown, setSidebarShown] = useState(false);

    const onToggleChange = useCallback(
        (e) => (e.target.checked ? setDarkTheme() : setLightTheme()),
        [setLightTheme, setDarkTheme],
    );

    useLockBodyScroll(sidebarShown);

    const showSidebar = useCallback(() => {
        setSidebarShown(true);
    }, [setSidebarShown]);
    const hideSidebar = useCallback(() => {
        setSidebarShown(false);
    }, [setSidebarShown]);

    const windowSize = useWindowSize();

    useEffect(() => {
        if (windowSize === windowSizes.desktop) {
            setSidebarShown(false);
        }
    }, [windowSize]);

    const { navs } = useThemeConfig();

    const brand = <Link to={'.'} className={style.brand}>
        <Logo className={style.logo} />
        <strong className={style.title}>{title}</strong>
    </Link>;

    const items = navs.map((nav, index) =>
        <NavLink className={style.link} key={`nav-${index}`} {...nav} />);

    return <div className={classNames(style.navbar, { [style.showSidebar]: sidebarShown })}>
        <div className={style.inner}>
            <div className={style.items}>
                {navs.length > 0 && <button tabIndex={0} onClick={showSidebar} className={style.toggle}>
                    <IoMenuSharp size={30} />
                </button>}
                {brand}
                {items}
            </div>
            <div className={style.items}>
                <Toggle
                    icons={{ checked: '🌜', unchecked: '🌞' }}
                    onChange={onToggleChange}
                    defaultChecked={isDarkTheme} />
                <Search />
            </div>
        </div>
        <div className={style.sidebarBackdrop} onClick={hideSidebar} />
        <div className={style.sidebar}>
            <div className={style.sidebarBrand}>
                {brand}
            </div>
            <div className={style.items}>
                {items}
            </div>
        </div>
    </div>;
}
